
.vue-tagger{background:#fcfcfc;background-clip:padding-box;border-radius:3px;border:1px solid rgba(0,0,0,0.15);box-shadow:inset 0 1px 2px rgba(0,0,0,0.1);display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;padding:6px 0 0 6px;max-width:400px;min-height:32px
}
.vue-tagger-tag{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;background:#708EA3;border-radius:2px;color:#fff;font-family:'Helvetica Neue';font-weight:bold;font-size:11px;letter-spacing:0.02em;padding:0 6px;margin-bottom:6px;margin-right:3px;height:26px
}
.vue-tagger-delete-tag{cursor:pointer;line-height:1;position:relative;top:-1px;margin-left:0.75em;padding:3px
}
.vue-tagger-input-container{-ms-flex-positive:1;flex-grow:1;-ms-flex-preferred-size:75px;flex-basis:75px;margin-bottom:6px;margin-right:6px;height:26px
}
.vue-tagger-input{-webkit-appearance:none;-moz-appearance:none;appearance:none;border:none;height:100%;outline:none;width:100%
}
.awesomplete [hidden]{display:none
}
.awesomplete .visually-hidden{position:absolute;clip:rect(0, 0, 0, 0)
}
.awesomplete{display:inline-block;width:100%;height:100%;position:relative
}
.awesomplete>ul{position:absolute;left:0;z-index:1;min-width:200px;max-width:200px;box-sizing:border-box;list-style:none;padding:0;margin:0;background:#fff
}
.awesomplete>ul:empty{display:none
}
.awesomplete>ul{border-radius:.3em;margin:.2em 0 0;background:rgba(255,255,255,0.9);background:linear-gradient(to bottom right, white, rgba(255,255,255,0.8));border:1px solid rgba(0,0,0,0.3);box-shadow:0.05em 0.2em 0.6em rgba(0,0,0,0.2);text-shadow:none
}
@supports (transform: scale(0)){
.awesomplete>ul[hidden],.awesomplete>ul:empty{opacity:0;display:block
}
}
.awesomplete>ul>li{font-family:'Helvetica Neue', 'Helvetica', 'Arial', sans;font-size:13px;position:relative;padding:.5em .5em;cursor:pointer
}
.awesomplete>ul>li:hover{background:rgba(112,142,163,0.7);color:#fff
}
.awesomplete>ul>li[aria-selected="true"]{background:#708EA3;color:#fff
}
.awesomplete mark{background:transparent;font-weight:bold
}
.awesomplete li:hover mark{background:transparent;font-weight:bold
}
.awesomplete li[aria-selected="true"] mark{background:transparent;font-weight:bold;color:inherit
}



